import DummyActionTypes from "./dummy.types";

interface DummyState {
  infoBox: string;
  hideBox: boolean;
}

interface DummyAction {
  type: string;
  payload?: any;
}

const INITIAL_STATE: DummyState = {
  infoBox: "",
  hideBox: false,
};

const DummyReducer = (state = INITIAL_STATE, action: DummyAction) => {
  switch (action.type) {
    case DummyActionTypes.SET_BOX_INFO:
      return {
        ...state,
        infoBox: action.payload,
      };
    case DummyActionTypes.HIDE_BOX:
      return {
        ...state,
        hideBox: !state.hideBox,
      };
    default:
      return state;
  }
};

export default DummyReducer;
