import React from "react";
import { HeadContainer, TabContainer, TopicContainer } from "../../App.styles";
import { Link } from "react-router-dom";

const HeaderPage = () => {
  React.useEffect(() => {
    if (process.env.NODE_ENV === "development") {
      console.info("ComponentTree:", "HeaderPage rendered !");
    }
  });

  return (
    <HeadContainer colSize="6">
      <TopicContainer>TOPIC</TopicContainer>
      <TabContainer>
        <Link to="/develop">Develop</Link>
        TAB_NAV
        <Link to="/">Dummy</Link>
      </TabContainer>
    </HeadContainer>
  );
};

export default HeaderPage;
