import React, { ChangeEvent } from "react";
import { Dispatch } from "redux";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { hideBox, setBoxInfo } from "../../redux/dummy/dummy.actions";
import {
  selectDummyBox,
  selectDummyInfoBox,
} from "../../redux/dummy/dummy.selector";
import {
  TopicContainer,
  Topic,
  ActionContainer,
  InputField,
  RealButton,
  MagicBox,
  FakeButton,
  SurpriseBox,
  DummyPageContainer,
} from "./dummyPage.styles";

interface Actions {
  setBoxInfo: (text: string) => {};
  setHideBox: () => {};
}

interface ReduxProps {
  infoBox: string;
  hideBox: boolean;
}

type DummyProps = Actions & ReduxProps;

const DummyPage = ({ ...props }: DummyProps) => {
  React.useEffect(() => {
    if (process.env.NODE_ENV === "development") {
      console.info("ComponentTree:", "DummyPage rendered !");
    }
  });

  const inputChange = (e: ChangeEvent<HTMLInputElement>) => {
    setBoxInfo(e.target.value);
  };

  const { infoBox, hideBox, setBoxInfo, setHideBox } = props;

  return (
    <DummyPageContainer>
      <TopicContainer>
        <Topic>Hello Dummy</Topic>
      </TopicContainer>

      <ActionContainer>
        <InputField
          type="text"
          name="dummyBox"
          className="input"
          onChange={(e: ChangeEvent<HTMLInputElement>) => {
            inputChange(e);
          }}
        />
        <RealButton
          className="realButton"
          onClick={() => {
            setHideBox();
          }}
        >
          {hideBox ? "Where is my box ?" : "Hide the box"}
        </RealButton>
      </ActionContainer>
      <MagicBox>
        {hideBox ? (
          <SurpriseBox>BÄÄM !!</SurpriseBox>
        ) : (
          <FakeButton>{infoBox !== "" ? infoBox : "PLACEHOLDER"}</FakeButton>
        )}
      </MagicBox>
    </DummyPageContainer>
  );
};

const mapStateToProps = createStructuredSelector({
  infoBox: selectDummyInfoBox,
  hideBox: selectDummyBox,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
  setBoxInfo: (text: string) => dispatch(setBoxInfo(text)),
  setHideBox: () => dispatch(hideBox()),
});

export default connect(mapStateToProps, mapDispatchToProps)(DummyPage);
