import React from "react";
import { Startpage, FooterContainer } from "./App.styles";
import HeaderPage from "./pages/header/header.page";
import AccountsPage from "./pages/accounts/accounts.page";
import MainPage from "./pages/main/main.page";
import FeaturesPage from "./pages/features/features.page";

const App = () => {
  React.useEffect(() => {
    if (
      process.env.REACT_APP_BUILD_VERSION &&
      process.env.NODE_ENV === "development"
    ) {
      console.info("Build Version:", process.env.REACT_APP_BUILD_VERSION);
    }
  });

  React.useEffect(() => {
    if (process.env.NODE_ENV === "development") {
      console.info("ComponentTree:", "App rendered !");
    }
  });

  return (
    <Startpage>
      <HeaderPage />
      <AccountsPage />
      <MainPage />
      <FeaturesPage />
      <FooterContainer>FOOTER</FooterContainer>
    </Startpage>
  );
};

export default App;
