import styled, { css } from "styled-components";

interface SizeProps {
  readonly colSize: string | number;
}

const justifyWidthInColumn = (props: any): any => {
  console.log("JustifyWidth", props);

  const widthMap: { [index: number]: string } = {
    1: "8.33%",
    2: "16.66%",
    3: "25%",
    4: "33.33%",
    5: "41.66%",
    6: "50%",
    7: "58.33%",
    8: "66.66%",
    9: "75%",
    10: "83.33%",
    11: "91.66%",
    12: "100%",
  };

  return css`
    width: ${widthMap[+props.colSize]};
  `;
};

export const Startpage = styled.div`
  background-color: lightgreen;
  margin: 0 auto;
  padding: 2%;
  display: flex;
  flex-flow: row wrap;
  min-width: 1200px;
  text-align: center;
  min-height: 600px;
`;

export const HeadContainer = styled.div<SizeProps>`
  flex: 1 100%;
  order: 0;
  display: flex;
  flex-flow: row wrap;
  text-align: center;
`;

export const TopicContainer = styled.div`
  flex: 1 100%;
  color: blue;
  font-size: 24px;
  order: 0;
`;

export const TabContainer = styled.div`
  flex: 1 100%;
  order: 1;
  color: red;
  background-color: yellow;
`;

export const AccountContainer = styled.div`
  flex: 1;
  order: 1;
  background-color: aqua;
`;

export const MainContainer = styled.div`
  flex: 3 0px;
  order: 2;
  overflow-y: scroll;
`;

export const FeatureContainer = styled.div`
  flex: 1;
  order: 3;
  background-color: lightgrey;
`;

export const FooterContainer = styled.div`
  flex: 1 100%;
  order: 4;
  background-color: red;
`;
