import styled, { css } from "styled-components";

const BaseButtonHeight = css`
  height: 50px;
`;

const BaseCornerRadius = css`
  border-radius: 18px;
`;
const BaseMarginTop = css`
  margin-top: 30px;
`;

export const DummyPageContainer = styled.div`
  font-size: 18px;
  width: 50vw;
  text-align: center;
  align-items: center;
  margin: 0 auto;
`;

export const TopicContainer = styled.div`
  width: 100%;
  text-align: center;
`;

export const Topic = styled.h1`
  color: rgba(50, 50, 247, 0.803);
  text-decoration-color: aqua;
`;

export const ActionContainer = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-between;
`;

export const InputField = styled.input`
  text-align: center;
  width: 60%;
  ${BaseButtonHeight}
  background-color: rgb(183, 181, 181);
  ${BaseCornerRadius}

  &:hover {
    background-color: rgb(93, 91, 91);
    color: white;
  }
`;

export const MagicBox = styled.div`
  width: 100%;
  height: 50px;
`;

export const SurpriseBox = styled.div`
  font-size: 25px;
  color: red;
`;

export const RealButton = styled.button`
  width: 30%;
  ${BaseButtonHeight}
  ${BaseCornerRadius}
`;

export const FakeButton = styled.button`
  ${BaseMarginTop}
  ${BaseButtonHeight}
  width: 100%;
  ${BaseCornerRadius}
`;
